import { Component, Input, OnInit, AfterViewInit, ViewEncapsulation } from '@angular/core';
import { environment } from '../../../../../environments/environment';
import {JSONBuilder} from '../../../+builder/services/JSONBuilder.service';
import {FeatureAuthService} from '../../../../shared/services/feature-access.service';
import {FeatureAccess} from "../../../../shared/interfaces/features.interface";
declare var jQuery: any;
@Component({
  selector: 'poweredby',
  template: `
		<div *ngIf="data.visible == true && (isBig||isSmall)" >
      <div class="stickyfooter" 
      [ngStyle]="{'background-color': isBig?'#fff':'transparent'}"
      >
        <div class="col1" ><a *ngIf="isBig||!isSmall" href="javascript:void(0);" class="create-link" [attr.data-text]="dataText"><span class="anim">{{dataText}}</span></a></div>
        <div class="col">
          <div [ngClass]="{
                            'powered-by': true,
                            'lightPowered': !isSmall 
                          }">
            <span 
              *ngIf="windowWidth > 480">
                BUILT WITH
            </span>
            <a target="_blank" href="http://rely.co">
              <img alt="Powered By" src="assets/images/builder/og-logo.png">
              <span class="poweredby-text">Outgrow</span>
            </a>
          </div>
        </div>
      </div>
    </div>
	`,
  encapsulation: ViewEncapsulation.None
})

export class PoweredByComponent implements OnInit, AfterViewInit {
  @Input() data: any;
  url: string;
  windowWidth: any = jQuery(window).width();
  dataText: string = (jQuery(window).width()>480)?
    (this.jsonBuilderHelper.getJSONBuilt().templateType=='Numerical'?'Create Your Own Calculator':'Create Your Own Quiz'):
    (this.jsonBuilderHelper.getJSONBuilt().templateType=='Numerical'?'Create Calculator':'Create Quiz');
  isBig:Boolean;
  isSmall:Boolean;
  ngOnInit() {
    this.url = environment.PROTOCOL + environment.APP_EXTENSION;
    this._featureAuthService.getAllFeatureAccess().subscribe((result) => {
      this._featureAuthService.features = new FeatureAccess(result);
      this.isBig=this._featureAuthService.features.custom_branding.logo_poweredby;
      this.isSmall=this._featureAuthService.features.custom_branding.cta_build_similar_calc;
      console.log(this.isBig,this.isSmall);
    });
  }
  constructor(private jsonBuilderHelper:JSONBuilder,private _featureAuthService:FeatureAuthService){}
  ngAfterViewInit() {

    let self = this;
    jQuery(window).on('resize', () => {
      this.windowWidth = jQuery(window).width();
      this.dataText = (this.windowWidth>480)?
        (this.jsonBuilderHelper.getJSONBuilt().templateType=='Numerical'?'Create Your Own Calculator':'Create Your Own Quiz'):
        (this.jsonBuilderHelper.getJSONBuilt().templateType=='Numerical'?'Create Calculator':'Create Quiz');
    });

  }
}



